<template>


  <div style="display: flex;justify-content: space-between;align-content: space-between;">
    <div style="width: 30%;"></div>

    <el-input v-model="query" placeholder="Buscar Grupo" style="--el-input-border-radius: 5px;margin-right: 5px;"></el-input>
    <el-tooltip
        effect="dark"
        content="Cadastrar Grupo"
        placement="bottom"
    >
      <el-button type="primary" @click="editGroupRef.newGroup()"><i class="fas fa-folder-plus"></i></el-button>
    </el-tooltip>


  </div>

  <div style="border: silver 1px solid; border-radius: 5px;margin-top: 20px;height: calc(100vh - 200px);">
    <div class="device">
      <div class="name" style="font-size: 12px;font-weight: 100;padding: 5px;width: calc(60% + 3.5px)">Titulo</div>
      <div class="name" style="font-size: 12px;font-weight: 100;padding: 5px;flex: 2;">Grupo Pai</div>
    </div>
    <div style="overflow-x: hidden;overflow-y: scroll;height: calc(100vh - 230px);">

      <div v-for="(device) in filteredDevices" :key="device.id" @click="editGroupRef.editGroup(device)" class="device">
        <div class="name" >{{device.name}}</div>
        <div  class="name" style="flex: 2;">
          {{store.getters['groups/getGroupNameById'](device.groupId)}}
        </div>
      </div></div>
  </div>

</template>

<script setup>

import {ref,computed,inject} from 'vue';
import {useStore} from "vuex"

const store = useStore();

const query = ref('');
const editGroupRef = inject('edit-group');




const filteredDevices = computed(()=>{
  if(query.value === ''){
    return store.state.groups.groupList;
  }else {
    return store.state.groups.groupList.filter((d) => {
      return d.name.toLowerCase().match(query.value.toLowerCase());
    });
  }

})



</script>

<style scoped>
.device{
  border-bottom: silver 1px solid;
  display: flex;
  flex-direction: row;
  text-align: center;
  cursor: pointer;
  margin-right: -1px;
}
.device:first-child{
  background: rgba(0,0,0,0.03)
}

.device:hover{
  background: rgba(0,0,0,0.03);
}



.device .name{
  font-size: 12px;
  padding: 7px;
  text-align: left;
  line-height: 14px;
  font-weight: 800;
  border-right: silver 1px dotted;
  width: 60%;
}

.icons{
  display: flex;
  justify-content: center;
  flex-direction: row-reverse;
  flex: 1;
  font-size: 11px;
}

.icons div{
  display: flex;
  justify-content: center;
  flex: 1;
  border-right: silver 1px dotted;
  padding: 7px;
  font-size: 11px;
}
.icons div i{
  font-size: 14px;
}

.icons div:first-child{
  border-right: none;
}

.icons div span{
  display: flex;
  padding: 2px;
  padding-left: 5px;
}

.subtitle{
  margin-top: 20px;
  font-weight: bold;
  font-size: 14px;
  text-transform: uppercase;
  color: var(--text-black);
}

.subtitle i{
  font-size: 12px;
  margin-right: 3px;
}

.updated{
  font-size: 12px;
  margin-top: 5px;
  color: var(--text-silver);
}

.address{
  color: var(--text-blue);
  font-size: 15px;
  margin-top: 10px;
  margin-bottom: 20px;
  padding: 10px;
  line-height: 20px;
}

</style>